.search {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 70px 0 43px;
  min-height: 232px;
  box-sizing: border-box;
  margin: 0 0 10px 0;
  width: 100%;
  max-width: 1140px;
}

.search__form {
  min-height: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 27px 0;
  background: #2F2F2F;
  border-radius: 9px;
}
.search__block-one {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
}
.search__block-two {
  display: flex;
  align-items: center;
  justify-content: center;
}
.search__icon {
  min-height: 34px;
  min-width: 34px;
  background-image: url(../../images/search.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-left: 19px;
}
.search__input {
  padding: 25px;
  width: 150%;
  border: none;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  color:#FFFFFF;
  background-color: #2F2F2F;
  border-radius: 9px;
}

.search__input:focus {
  outline: none;
}

.search__input::placeholder {
  color:#8B8B8B;
}
.search__line {
  width: 40px;
  height: 1px;
  background: #424242;
  transform: rotate(90deg);
}
.search__button {
  border: none;
  box-shadow: none;
  min-height: 34px;
  min-width: 80px;
  border-radius: 48px;
  background: #4285F4 url(../../images/search.svg) no-repeat center;
  color: #FFFFFF;
  transition: opacity 0.6s linear;
  margin: 10px 10px 10px 0;
  padding: 0;
}

.search__button:hover {
  opacity: 0.7;
  cursor: pointer;
}

.search__button:focus {
  outline: none;
}

.search__tumbler {
  position: relative;
  display: inline-block;
  width: 36px;
  height: 20px;
}

.search__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(170,170,170,0.1);
  box-shadow: 0 4px 4px rgba(50, 50, 71, 0.08), 0 4px 8px rgba(50, 50, 71, 0.06);
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 16px;
  transform: scale(-1, -1)
}

.search__slider::before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 2px;
  bottom: 2px;
  background-color: #2BE080;
  border-radius: 50%;
  border: #FFFFFF 1px solid;
}

.search__checkbox {
  display: none;
}

.search__checkbox:checked + .search__slider {
  background-color: #2BE080;
}

.search__checkbox:checked + .search__slider::before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
}

.search__label-text {
  margin-left: 14px;
  margin-right: 30px;
  font-weight: 400;
  font-size: 13px;
  line-height: 1.4;
  color: #FFFFFF;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .search {
    padding: 70px 0 63px;
    min-height: 240px;
  }

  .search__input {
    width: 55%;
  }
  .search__checkbox:checked + .search__slider::before {
    -webkit-transform: translateX(10px);
    -ms-transform: translateX(10px);
    transform: translateX(10px);
  }
}

@media screen and (min-width: 310px) and (max-width: 767px) {
  .search {
    padding: 70px 0 0 0;
    min-height: 240px;
    border-bottom: #424242 1px solid;
  }
  .search__form {
    flex-direction: column;
    margin: 0;
    background-color: transparent;
  }
  .search__icon {
    display: none;
  }
  .search__block-one {
    width: 100%;
    border-radius: 9px;
    height: 72px;
    background: #2F2F2F;
  }
  .search__block-two {
    width: 100%;
    height: 120px;
    background-color: #202020;
  }
  .search__line {
    display: none;
  }

  .search__input {
    padding: 20px 12px;
    font-size: 16px;
    line-height: 1;
  }

  .search__button {
    min-height: 44px;
    min-width: 80px;
    margin: 6px 19px 0 0;
  }

  .search__label-text {
    font-size: 11px;
    line-height: 1.6;
  }
}
