.movies-card {
  max-width: 364px;
  min-height: 271px;
  display: grid;
  grid-template-columns: repeat(2,minmax(min-content, max-content));
  grid-template-rows: repeat(3, minmax(min-content, max-content));
  background-color: #222222;
}

.movies-card__image {
  grid-row: 3;
  grid-column: 1/3;
  object-fit: cover;
  max-width: 364px;
  min-height: 151px;
}

.movies-card__title {
  grid-row: 1;
  grid-column: 1;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  color: #FFFFFF;
  margin: 14px 0 9px 12px;
  padding: 0;
  max-width: 220px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.movies-card__button {
  grid-row: 1/2;
  grid-column: 2;
  height: 30px;
  width: 30px;
  box-sizing: border-box;
  box-shadow: none;
  border: none;
  padding: 0;
  margin: 14px 12px 0 10px;
  justify-self: right;
  transition: opacity 0.6s linear;
  border-radius: 36px;
}

.movies-card__button:hover {
  opacity: 0.7;
  cursor: pointer;
}

.movies-card__button:focus {
  outline: none;
}

.movies-card__button_favorites {
  background: #313131 url(../../images/favorites-empty.svg) no-repeat center;
}

.movies-card__button_favorites-on {
  background: #3DDC84 url(../../images/favorites-fill.svg) no-repeat center;
}

.movies-card__button_delete {
  width: 30px;
  height: 30px;
  background: #313131 url(../../images/remove.svg) no-repeat center;
}

.movies-card__duration {
  grid-row: 2;
  grid-column: 1;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  color: #8B8B8B;
  margin: 0 0 14px 14px;
  padding: 0;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .movies-card {
    max-width: 339px;
    min-height: 257px;
  }

  .movies-card__image {
    max-width: 339px;
    min-height: 190px;
  }

  .movies-card__title {
    max-width: 289px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .movies-card {
    max-width: 300px;
    min-height: 235px;
  }

  .movies-card__image {
    max-width: 300px;
    min-height: 168px;
  }

  .movies-card__title {
    max-width: 235px;
  }
}
