.movies {
  max-width: 1280px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 70px;
  margin: auto;
  box-sizing: border-box;
}

.movies__more{
  width: 100%;
  max-width: 320px;
  min-height: 36px;
  margin: 0 0 80px 0;
  box-shadow: none;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  transition: opacity 0.6s linear;
  background: #2F2F2F;
  color: #FFFFFF;
}

.movies__more:hover {
  opacity: 0.7;
  cursor: pointer;
}

.movies__more:focus {
  outline: none;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .movies {
    padding: 0 30px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .movies {
    padding: 0 10px;
  }
}
