.portfolio {
  display: flex;
  justify-content: center;
  background-color: #202020;
  min-height: 425px;
}

.portfolio__container {
  width: 100%;
  max-width: 1280px;
  box-sizing: border-box;
  padding: 0 70px;
}

.portfolio__title {
  font-weight: 400;
  font-size: 18px;
  line-height: 20px;
  color: #A0A0A0;
  margin: 0 0 50px 0;
  padding: 0;
}

.portfolio__links {
  list-style: none;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

.portfolio__item {
  margin: 0 0 20px 0;
  min-height: 69px;
}

.portfolio__item:last-child {
  margin: 0;
}

.portfolio__link {
  color: #FFFFFF;
  display: block;
  width: 100%;
  font-weight: 400;
  font-size: 30px;
  line-height: 38px;
  text-decoration: none;
  margin: 0;
  padding: 0 0 19px 0;
  background: right 12px/24px 24px url(../../images/arrow.svg) transparent no-repeat;
  border-bottom: 1px solid #424242;
  box-sizing: border-box;
  transition: opacity 0.6s linear;
}

.portfolio__link:hover {
  opacity: 0.7;
  cursor: pointer;
}

.portfolio__link:focus {
  background-color: transparent;
  outline: none;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .portfolio {
    min-height: 390px;
  }

  .portfolio__container {
    padding: 0 50px;
  }

  .portfolio__link {
    font-size: 28px;
    line-height: 34px;
    background: right 12px/22px 22px url(../../images/arrow.svg) transparent no-repeat;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .portfolio {
    min-height: 294px;
  }

  .portfolio__container {
    padding: 0 14px;
  }

  .portfolio__title {
    font-size: 14px;
    line-height: 18px;
    margin: 0 0 40px 0;
  }

  .portfolio__item {
    margin: 0 0 21px 0;
    min-height: 28px;
  }

  .portfolio__link {
    font-size: 18px;
    line-height: 24px;
    background: right 8px/12px 12px url(../../images/arrow.svg) transparent no-repeat;
  }
}
